.Concepteur {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    width: 90%;
}
.Concepteur p {
    text-align: center;
    width: 30%;
}
.Titre{
    text-align: center;
    margin: 20px;
}
.Concepteur span {
    font-weight: 600;
    color: #D72F2F;
}
.Concepteur img {
    height: 150px;
    width: 150px;
    border: 2px solid #D72F2F;
    border-radius: 128px;
}


